'use strict';

/**
* POSIX path delimiter.
*
* @module @stdlib/constants-path-delimiter-posix
* @type {string}
*
* @example
* import PATH_DELIMITER_POSIX from '@stdlib/constants-path-delimiter-posix' ;
*
* var PATH = '/usr/bin:/bin:/usr/sbin:/sbin:/usr/local/bin';
* var paths = PATH.split( PATH_DELIMITER_POSIX );
* // returns ['/usr/bin','/bin','/usr/sbin','/sbin','/usr/local/bin']
*/

// MAIN //

/**
* Path delimiter on POSIX platforms.
*
* @constant
* @type {string}
* @default ':'
*/
var PATH_DELIMITER_POSIX = ':';


// EXPORTS //

export default PATH_DELIMITER_POSIX;
